/* Oxanium Font Import */
@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Oxanium:wght@400;700&display=swap");


/* base styles */
* {
  margin: 0;
  font-family: "Oxanium";
  background-color: #1f2833;
  color: #c5c6c7
  }

.navbar {
  margin-top: 5%;
  margin-left: 15.1%;
  margin-right: 15.1%;
  display: grid;
  grid-template-columns: 2fr 6fr;
}

.navbar h1 {
  color: #c5c6c7;
  text-decoration: underline;

}

.navbar .links  {
  margin-left: auto;

}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #1f2833;
  background-color: #c5c6c7;
  border-radius: 8px;

}

.navbar a:hover {
  color: #e3e8f3;
  text-decoration: underline;
}


.content {
  max-width: 68%;
  margin: 1% auto;
  padding: .5em;
  text-align: -webkit-center;
  border: solid #c5c6c7 2px;
  border-radius: 10px;

}

.banner {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.myWork img {
  border-bottom: 3px solid;
  max-width: 75%;
  max-height: 70%;
  border-radius: 6px;
  box-shadow: 4px blanchedalmond;
  margin: 1em;

}

.myWork img:hover {
  transform: scale(1.25, 1.25);
}

.myWork img:active {
  transform: scale(0.9, 0.9);
}

.myWork {
  display: grid;
  grid-template-rows: .1fr .1fr .1fr .05fr;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: left;

}

body .contact {
  color: #c5c6c7;
}

body .contactContainerForm {
  background-color:#c5c6c7;
  width: 20%;
  height: 100%;

}

.contact-form{

  color: #000000;
 
}
.form-control{
  background-color: #c5c6c7;
  border-radius: 8px;
  color: #000000;
}

.btn
{
  color: #1f2833;
  background-color: #c5c6c7;
  border-radius: 8px;
}

h2.top-title{
  
 color: #c5c6c7;
  
}